<template>
	<div id="app">
		<div class="imtoken" v-if="weixin">
			{{$t('text5')}}
			<img src="@/assets/line.png" />
		</div>
		<div class="title">
			<img src="@/assets/titleLogo.png" />
			<div>
				<span @click="switchLanguage('zh')" :class="{active:$i18n.locale == 'zh'}">简体中文</span>
				<span class="line">|</span>
				<span @click="switchLanguage('en')" :class="{active:$i18n.locale == 'en'}">English</span>
			</div>
		</div>
		<div class="content">
			<img src="@/assets/centerLogo.png" />
			<p>{{$t('text1')}}</p>
			<p>{{$t('text2')}}</p>
			<div @click="download">
				<img src="@/assets/androidLogo.png" />
				<span>{{$t('text3')}}</span>
			</div>
		</div>
		<div class="bottom" v-html="$t('text4')"></div>
	</div>
</template>

<script>
export default {
	data(){
		return {
			appDownloadAddress: 'https://dtttesttt.oss-cn-shenzhen.aliyuncs.com/app/dfpro_latest.apk',
			weixin: false,
		}
	},
	methods:{
		switchLanguage(type){
			localStorage.setItem('locale',type)
			this.$i18n.locale = type
		},
		download(){
			let link = document.createElement('a')
			link.href = this.appDownloadAddress
			link.click()
		}
	},
	mounted() {
		//判断是否是微信
		var ua = navigator.userAgent.toLowerCase();
		if(ua.match(/MicroMessenger/i) == "micromessenger") {
			this.weixin = true;
		}
	}
}
</script>

<style>
	@import './assets/css/App.css';
</style>
